import { validateValues } from "../Hooks/useJoin";

export default function validate({ username, password, passwordCheck }: validateValues) {
  let errors = {
    username,
    password,
    passwordCheck,
  };

  if (!username) {
    errors.username = "*  .";
  } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(username)) {
    errors.username = "*   .";
  } else {
    errors.username = ''
  }

  if (!password) {
    errors.password = "*  .";
  } else if (password.length < 8) {
    errors.password = "*8    .";
  } else {
    errors.password = ''
  }

  if (!passwordCheck) {
    errors.passwordCheck = "*  .";
  } else if (passwordCheck.length < 8) {
    errors.passwordCheck = "*8    .";
  } else {
    errors.passwordCheck = ''
  }

  return errors;
}